.player {
    display: flex;
    align-items: center;
    margin-top: 16px;
    .player-left {
        min-width: 170px;
        height: 170px;
        background-color: #202f4e;
        border-radius: 20px;
        margin-right: 34px;
        display: flex;
        padding: 60px 19px 64px 20px;
        img {
            width: 131px;
            height: 46px;
        }
    }
    .player-right {
        margin-top: 15px;
        span.title {
            font-family: $font-family-bold;
            font-size: 24px;
            line-height: 30px;
            color: #202f4e;
            margin-bottom: 3px;
            display: block;
        }
        p.excerpt {
            font-size: 20px;
            line-height: 25px;
            color: #858484;
            margin-bottom: 23px;
            max-width: 954px;
        }
        .plays {
            display: flex;
            align-items: center;
            button {
                width: 27px;
                border: none;
                img {
                    width: 27px;
                    height: 27px;
                    cursor: pointer;
                }
            }
            .btn-click {
                display: none;
                &.active {
                    display: flex;
                }
            }
            .play {
                align-items: center;
                justify-content: center;
                background-color: #202f4e;
                width: 27px;
                height: 27px;
                cursor: pointer;
                span {
                    display: block;
                    border: 6px solid transparent;
                    border-left: 9px solid #fff;
                    margin-left: 7px;
                    margin-top: 2px;
                }
            }
            .pause {
                margin-right: 2px;
                background-color: #5379f9;
                width: 27px;
                height: 27px;
                margin: 0;
                cursor: pointer;
                position: relative;
                &::before {
                    content: "";
                    position: absolute;
                    top: 8px;
                    left: 8px;
                    width: 3px;
                    height: 12px;
                    background-color: #fff;
                }
                &::after {
                    content: "";
                    position: absolute;
                    top: 8px;
                    left: 16px;
                    width: 3px;
                    height: 12px;
                    background-color: #fff;
                }
            }
            .block-line {
                display: flex;
                .treck-top {
                    position: absolute;
                    background-image: url(../img/black.png);
                    background-repeat: repeat-x;
                    height: 30px;
                    margin-left: -2px;
                    top: -55%;
                    z-index: 20;
                }
                .treck-button {
                    position: absolute;
                    background-image: url(../img/gray.png);
                    background-repeat: repeat-x;
                    width: 100%;
                    height: 30px;
                    top: -50%;
                    z-index: 10;
                }
            }
            .track {
                a img {
                    cursor: pointer;
                }
                .share {
                    display: none;
                    .tooltip {
                        position: relative;
                        display: inline-block;
                    }
                    
                    .tooltip .tooltiptext {
                        visibility: hidden;
                        width: 140px;
                        background-color: #555;
                        color: #fff;
                        text-align: center;
                        border-radius: 6px;
                        padding: 5px;
                        position: absolute;
                        z-index: 1;
                        bottom: 150%;
                        right: 100%;
                        opacity: 0;
                        transition: opacity 0.3s;
                    }
                    
                    .tooltip .tooltiptext::after {
                        content: "";
                        position: absolute;
                        top: 100%;
                        left: 50%;
                        margin-left: -5px;
                        border-width: 5px;
                        border-style: solid;
                        border-color: #555 transparent transparent transparent;
                    }
                    
                    .tooltip:hover .tooltiptext {
                        visibility: visible;
                        opacity: 1;
                    }
                }
                &.active {
                    position: relative;
                    .share {
                        display: flex;
                        position: absolute;
                        top: -5px;
                        background:#858484;
                        padding: 5px;
                        z-index: 10;
                        border-radius: 5px;
                        input {
                            border: none;
                            outline: none;
                        }
                        .tooltip {
                            .copy {
                                display: flex;
                                align-items: center;
                                justify-content: center;
                            }
                        }
                    }
                }
            }
        }

        .audio-track {
            cursor: not-allowed;
            width: 427px;
            height: 15px;
            position: relative;
            display: flex;
            align-items: center;
            margin-left: 12px;
            margin-right: 12px;
            .time {
                position: absolute;
                width: 0;
                height: 30px;
            }
            .current-time {
                position: absolute;
                top: -25px;
                font-weight: 400;
                font-size: 12px;
                line-height: 25px;
            }
            &.active {
                cursor: pointer;
            }
        }
        .time-block {
            display: flex;
            justify-content: space-between;
            font-weight: 400;
            font-size: 12px;
            line-height: 25px;
            width: 465px;
            .start-time {
                margin-left: 38px;
            }
        }
    }
}

@media(min-width:1000px) {
    .player .player-right .plays .track.active .share {
        left: 35px;
    }
}
@media(max-width:999px) {
    .player .player-right .plays .track.active .share {
        right: 0;
        z-index: 10;
    }
}
@media (max-width:768px) {
    .single__podcasts .podcasts-posts {
        padding: 0;
    }
    .podcasts-posts {

        border-radius: 20px;
        .player {
            .number {
                display: none;
            }
            flex-direction: column;
            .player-left {
                margin-right: 0;
                width: 100%;
                height: auto;
                min-height: 100px;
                border-radius: 20px 20px 0 0;
                padding: 20px;
                display: flex;
                align-items: center;
                justify-content: center;
                img {
                    max-width: 100%;
                    width: 100%;
                    height: auto;
                }
            }
            .player-right {
                width: 100%;
                padding: 10px 15px;
                span.title {
                    font-size: 15px;
                    line-height: 20px;
                }
                p.excerpt {
                    font-size: 13px;
                    line-height: 18px;
                }
            }
        }
    }
    
}
@media (max-width:600px) {
    .single-header.podcast {
        padding-top: 40px;
    }
    .podcasts-posts {
        .player {
            .player-right {
                .plays {
                    .audio-track {
                        width: 100%;
                        max-width: 427px;
                    }
                }
                .time-block {
                    max-width: 465px;
                    width: 100%;
                    padding-right: 35px;
                    font-size: 10px;
                    line-height: 20px;
                }
            }
        }
    } 
}
@media (max-width:390px) {
    .single-header.podcast .single-header__block .single-header__block .box-text .box-btn {
        flex-wrap: wrap;
        justify-content: center;
        .play__podcast {
            margin: 0 auto 10px;
        }
    }
}

@media (max-width: 768px) {
    .home__podcasts-posts {
        .player {
            margin-right: 20px;
            background: #FFFFFF;
            border-radius: 10px;
            margin: 30px 20px 40px 0;
            .player-left {
                width: 100%;
                justify-content: center;
                align-items: center;
                padding: 25px 55px;
                height: 82px;
                border-radius: 20px 20px 0 0;
                img {
                    max-width: 100%;
                    height: auto;
                }
            }
            .player-right {
                margin-top: 25px;
                margin-bottom: 22px;
                padding: 0 22px;
                .title {
                    font-size: 15px;
                    line-height: 20px;
                }
                .excerpt {
                    font-size: 13px;
                    line-height: 18px;
                }
                .plays, .time-block {
                    display: none;
                }
            }
            
        }
    }
}