
.header {
    padding: 0;
}
header {
    .navmenu {
        height: 100px;
        .navbar {
            height: 100%;
            nav {
                height: 100%;
                ul.nav-list {
                    height: 100%;
                    li.menu-item {
                        display: flex;
                        align-items: center;
                        height: 100%;
                    }
                    li.current-menu-item {
                        position: relative;
                        
                        &::after {
                            content:"";
                            background: linear-gradient(90deg, #DF6449 0%, #EFB12F 106.8%);
                            width: 100%;
                            height: 4px;
                            position: absolute;
                            bottom: 0;
                            right: 0;
                            left: 0;
                        }
                    }
                }
            }
        }
    }

}
@media (min-width:990px) {
    header.header__search .navmenu .search {
        height: 100%;
        display: flex;
        align-items: center;
        position: relative;    
        &::after {
            content:"";
            background: linear-gradient(90deg, #DF6449 0%, #EFB12F 106.8%);
            width: 100%;
            height: 4px;
            position: absolute;
            bottom: 0;
            right: 0;
            left: 0;
        }
    }
}
@media (max-width:990px) {
    header {
        .navmenu {
            height: 100px;
            .navbar {
                height: 100%;
                &.open__menu {
                    height: min-content;
                }
                nav {
                    min-height: 100%;
                    ul.nav-list {
                        min-height: 100%;
                        li.menu-item {
                            display: flex;
                            align-items: center;
                            height: 100%;
                        }
                        li.current-menu-item {
                            position: relative;
                            
                            &::after {
                                content:"";
                                background: linear-gradient(90deg, #DF6449 0%, #EFB12F 106.8%);
                                width: 0%;
                                height: 0px;
                                position: relative;
                                bottom: 0;
                                right: 0;
                                left: 0;
                            }
                        }
                    }
                }
            }
        }
